<template>
  <div class="table1">
    <div class="add cb">
      <div class="add-left fl">请假管理</div>
      <div class="add-right fr">
        <button @click="dialogFormVisible = true">新增</button>   
      </div>
    </div>
    <el-table ref="multipleTable" :data="LeaveList" tooltip-effect="dark" type="index" style="width: 100%">
        <el-table-column type="selection" width="70">
      </el-table-column>
       <el-table-column prop="staff" label="请假人" show-overflow-tooltip>
        <template scope="scope">
          <div>{{scope.row|showStaffName}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="beginLeaveDate" label="请假开始时间" show-overflow-tooltip>
        <template scope="scope" >
          <div>{{scope.row.beginLeaveDate}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="endLeaveDate" label="请假结束时间" show-overflow-tooltip>
        <template scope="scope">
          <div >{{scope.row.endLeaveDate}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="leaveDay" label="请假天数"  width="110">
        <template scope="scope">
          <div >{{scope.row.leaveDay}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="staffGroupType" label="归属小组"  width="120">
        <template scope="scope">
          <div>{{scope.row.staffGroupType}}</div>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="140">
        <template scope="scope">
            <el-button type="text" @click="deletelist(LeaveList.staffId,scope.$index)"><i class="el-icon-delete delete"></i></el-button>
        </template>
			</el-table-column>
		</el-table>
    <el-dialog title="新增" :visible.sync="dialogFormVisible" width="60%" size="mini" class="main-content">
      <el-form :label-position="labelPosition" :inline="true" label-width="130px" :rules="rules" :model="addDetail" size="mini" ref="formName">
        <el-form-item label="请假人"  prop="staffname">
          <el-input v-model="addDetail.staffname" placeholder="" name="createStaffId" :disabled="true"></el-input>
        </el-form-item>      
        <el-form-item label="归属组" prop="staffGroupType">
          <template>
            <el-select v-model="addDetail.staffGroupType" placeholder="请选择归属小组"  >
              <el-option v-for="item in staffGroupType" :key="item.typeId" :label="item.typeValue"  :value="item.typeId"></el-option>
            </el-select>
          </template>
        </el-form-item>

        <el-form-item label="请假开始日期" prop="beginLeaveDate" >
          <template>
           <el-date-picker  v-model="addDetail.beginLeaveDate" type="date"  placeholder="选择开始日期" value-format="yyyy-MM-dd">
           </el-date-picker>
          </template>
        </el-form-item> 
        <el-form-item label="请假结束日期" prop="endLeaveDate" >
          <template>
           <el-date-picker  v-model="addDetail.endLeaveDate" type="date"  placeholder="选择结束日期" value-format="yyyy-MM-dd">
           </el-date-picker>
          </template>
        </el-form-item>
        <div class="leave">一共请假了<span>{{ leaveDays  }}</span>天</div>                  
      </el-form>
      <span slot="footer" class="dialog-footer">
            <el-button @click="adddetail(addDetail,'formName')" size="small">新增</el-button>
            <el-button type="primary" @click="dialogFormVisible= false" size="small">取消</el-button>
          </span>
    </el-dialog>
    <div class="hello">      
      <el-pagination  @size-change="pageSizeChange" @current-change="currentPageChange" :current-page="currentPage" :page-sizes="[ 5,10 ]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination> 
    </div>
  </div>
</template>

<script>
import service from "../scripts/service.js";
export default {
  name: "table1",
  data() {
    return {
      rules: {
        beginLeaveDate: [
          {
            required: true,
            message: "请选择请假开始日期",
            trigger: "blur"
          }
        ],
        endLeaveDate: [
          {
            required: true,
            message: "请选择请假结束日期",
            trigger: "blur"
          }
        ],
        staffGroupType: [
          {
            required: true,
            message: "请选择归属小组",
            trigger: "blur"
          }
        ]
      },
      dialogVisible: false,
      dialogFormVisible: false,

      addDetail: {
        beginLeaveDate: "",
        endLeaveDate: "",
        staffId: "",
        staffGroupType: "",
        beginDate: "",
        leaveDay: ""
      },
      staffId: "",
      staffGroupType: "",
      beginDate: "",
      editDetail: {},
      index: 0,
      LeaveList: [],
      subjectName: "",
      staffname: "",
      staffID: "",
      editid: "",
      //多选值
      multipleSelection: [],
      //当前页
      currentPage: 1,
      //分页大小
      pageSize: 10,
      //总记录数
      total: ""
    };
  },
  mounted: function() {
    this.getselect();
    this.getAauthor();
  },
  filters: {
    showStaffName(row) {
      if (row.staff) {
        return row.staff.staffname;
      } else {
        return "";
      }
    }
  },
  computed: {
    // 计算属性的 getter
    leaveDays: function() {
      // `this` 指向 vm 实例
      if (
        this.addDetail.endLeaveDate.length == 0 ||
        this.addDetail.beginLeaveDate.length == 0
      ) {
        return "0";
      } else {
        return parseInt(
          (new Date(this.addDetail.endLeaveDate.replace(/-/g, "/")).getTime() -
            new Date(
              this.addDetail.beginLeaveDate.replace(/-/g, "/")
            ).getTime()) /
            (1000 * 60 * 60 * 24)
        );
      }
    }
  },
  methods: {
    getselect() {
      service
        .getListGroupType()
        .then(res => {
          this.staffGroupType = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    getAauthor() {
      service
        .getListSxByGType()
        .then(res => {
          console.log(res);
          this.Aauthor = res.data[4];
          console.log(this.Aauthor);
          this.staffname = this.Aauthor.staffname;
          this.staffID = this.Aauthor.staffId;
          this.getList();
          this.addDetail = {
            staffname: this.staffname,
            beginLeaveDate: "",
            endLeaveDate: "",
            leaveDay: "",
            staffGroupType: ""
          };
        })
        .catch(err => {
          console.log(err);
        });
    },
    getList() {
      let params = {
        pages: this.currentPage,
        pageSize: this.pageSize,
        staffId: this.staffID
      };
      console.log(params);
      service
        .getStaffLeaveList(params)
        .then(res => {
          console.log(res);
          // 将获取回来的数据赋值给list
          this.LeaveList = res.data.list;
          this.total = res.data.total;
          console.log(this.LeaveList);
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 删除
    deletelist(staffId, index) {
      this.$confirm("确认删除？")
        .then(_ => {
          let params = {
            leaveId: this.LeaveList[index].leaveId
          };
          console.log(params);
          service
            .delOneCsl(params)
            .then(response => {
              console.log(response);
              // 加载列表
              this.getAauthor();
            })
            .catch(error => {
              console.log(error);
              alert("删除失败！");
            });
        })
        .catch(_ => {});
    },
    //新增
    adddetail(formName) {
      if (
        this.addDetail.beginLeaveDate.substring(0, 7) ==
        this.addDetail.endLeaveDate.substring(0, 7)
      ) {
        if (
          this.addDetail.beginLeaveDate.substring(8, 10) <
          this.addDetail.endLeaveDate.substring(8, 10)
        ) {
          this.$refs["formName"].validate(valid => {
            if (valid) {
              let s1 = this.addDetail.beginLeaveDate;
              console.log(this.addDetail.beginLeaveDate);

              s1 = new Date(s1.replace(/-/g, "/"));
              let s2 = this.addDetail.endLeaveDate;
              s2 = new Date(s2.replace(/-/g, "/"));
              var days = s2.getTime() - s1.getTime();
              this.addDetail.leaveDay = parseInt(days / (1000 * 60 * 60 * 24));
              console.log(this.addDetail.leaveDay);

              this.addDetail = {
                leaveDay: this.addDetail.leaveDay,
                beginLeaveDate: this.addDetail.beginLeaveDate,
                endLeaveDate: this.addDetail.endLeaveDate,
                staffGroupType: this.addDetail.staffGroupType,
                checkStaffId: this.staffID
              };
              let params = {
                csl: this.addDetail
              };
              console.log(params);
              service
                .addOneCsl(params)
                .then(response => {
                  alert("submit!");
                  this.getAauthor();
                })
                .catch(error => {
                  console.log(error);
                });
              this.dialogFormVisible = false;
            } else {
              return false;
            }
          });
        } else {
          console.log("请选择正确的日期");
        }
      } else {
        console.log("不能跨月请假");
      }
    },
    //分页大小修改事件
    pageSizeChange: function(val) {
      console.log("每页 " + val + " 条");
      this.pageSize = val;
      this.getList();
    },
    //当前页修改事件
    currentPageChange: function(val) {
      this.currentPage = val;
      console.log("当前页: " + val);
      this.getList();
    }
  }
};
</script>

<style scoped>
table {
  width: 100%;
  font-size: 14px;
  border: 1px solid #eee;
}
table thead th {
  background: #e5eff8;
  padding: 22px 18px 22px 18px;
  text-align: center;
}
table tbody td {
  padding: 10px 0px 10px 18px;
  text-align: left;
  border-bottom: 1px solid #eee;
  border-right: 1px solid #eee;
}
table span {
  margin: 0 10px;
  cursor: pointer;
}
.delete {
  color: red;
}
.edit {
  color: #008cd5;
}
.add {
  border: 1px solid #eee;
  margin-bottom: 10px;
  height: 65px;
  padding: 15px 0px 15px 15px;
}
tbody tr:hover {
  background-color: #e5f0f86c;
}
.add button {
  background: #008cd5;
  border: 0;
  cursor: pointer;
  padding: 6px 15px;
  border-radius: 3px;
  color: #fff;
  margin-left: 10px;
}
.hello {
  margin-top: -10px;
  margin-bottom: 10px;
}
.header-search-input {
  display: inline-block;
  right: 1%;
  margin-left: 12px;
  width: 300px;
}
.header-search-input input {
  position: relative;
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid #d8dce5;
  display: inline-block;
  height: 35px;
  line-height: 1;
  outline: 0;
  padding: 0 25px 0 15px;
  width: 280px;
  margin-right: -4px;
}
.query-search-btn {
  color: #c2d6e7;
  position: relative;
  display: inline-block;
  margin-left: -30px;
  cursor: pointer;
}
.add-left {
  font-size: 16px;
}
.hello {
  position: fixed;
  bottom: 0px;
  right: 20px;
}
.add-right {
  margin-right: 80px;
}
.leave {
  margin-left: 50px;
}
.leave span {
  color: red;
}
</style>


